.P-login {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
}

.P-login-modal-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: .4;
}

.P-login-modal-container {
  min-width: 500px;
  min-height: 600px;
  position: relative;
  background-color: #2D2F33;
  position: relative;
  overflow: hidden;
}

// registration title content

.P-login-register {
  width: 100%;
  font-family: cursive;
  font-size: 24px;
  color: #e74c3c;
  margin: 30px 0 0 0;
  transition: 0.3s;

  &:hover{
    color: #761911;
  }
}

